import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {ChromePicker} from 'react-color';

const SliderColor = ({playerRef, extractedColor="#1f3fab"}) => { 
    let [color, setColor] = useState({hex: extractedColor});

    const onChange = useCallback((c) => {
       setColor(c);
    }, [setColor]);

    useEffect(() => {
        playerRef.current.contentWindow.postMessage({
            type: "slider-color",
            body: color.hex
        }, "*");
    }, [color]);

    useEffect(() => {
        if (extractedColor) {
            console.log('setting extracted color=', extractedColor);
            setColor({hex: extractedColor});
        }
    }, [extractedColor, setColor]);

    return (
        <div className="z-color">
          <div
            onMouseDown={e => e.stopPropagation()}
            className="picker-container">
            <ChromePicker color={color} onChange={onChange}></ChromePicker>
          </div>
        </div>);
};

export default SliderColor;
