import React, { useRef, useEffect, useCallback, useState} from 'react';
import {useCoverArtColors} from 'components/hooks/useCoverArtColors.js';
import SliderColor from './SliderColor.js';
import {connect} from 'react-redux';
import BuildPlayer from './BuildPlayer.js';
import Shuffle from 'components/nft-jacker/ShuffleButton.js';
import SkinImage from './SkinImage.js';
import './build.scss';

const Build = (props) => { 
    let playerRef = useRef();
    let [imageHash, setImageHash] = useState();
    let {
        mainColor
    } = useCoverArtColors({coverArt: imageHash ? "https://zequencer.io/ipfs/" + imageHash : undefined});

    return (
        <div className="nft-jacker build-a-spore jacked">
          <div className="center-builder">
            <SkinImage
              imageHash={imageHash}
              setImageHash={setImageHash}
              playerRef={playerRef}/>
            <BuildPlayer playerRef={playerRef}/>
            <SliderColor
              extractedColor={mainColor}
              playerRef={playerRef}/>
          </div>
          <div className="mint-button">
            Mint
          </div>
          <Shuffle buttonTextColor="white" playerRef={playerRef}/>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Build);
