import React, { useEffect, useCallback, useState} from 'react';
import {useAudioPlayer} from 'components/hooks/useAudioPlayer.js';
import AudioPlayer from 'components/spore/AudioPlayer.js';
import {connect} from 'react-redux';
import './player.scss';
import ShareButton from '../nft-jacker/ShareButton';

const RemixPlayer = ({remix, setRemix, songName, artist, externalURL}) => { 
    console.log('spore hash recording data =', remix.sporeHash);
    let {audioPlayer, dispose} = useAudioPlayer(remix.sporeHash);
    console.log(remix.sporeId);
    return (
        <div className="remix-player">
          <div className="pushable close-button" onClick={() => setRemix(undefined)}>x</div>
          <ShareButton
            url={"spores.vision/play/" + remix.collectionAddress + "/" + remix.tokenId + "/" + remix.sporeId}
            type={"remix"}/>
          <AudioPlayer
            externalURL={externalURL}
            artist={artist}
            songName={songName}
            creator={remix.creator} audioUnit={audioPlayer} spore={remix} sporeMetadata={remix} />
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixPlayer);
